.wrapper {
    margin: 5%;
}

.table .line {
    display: flex;
}

.table .line .counters {
    display: flex;
    padding: 0;
}

.table .line .event-name {
    display: flex;
}

.table .line .level-1 h6 {
    font-weight: 650;
    padding: 1em 1em 0 1em;
}

.table .line .level-2 {
    padding-left: 3em;
}

.table .line .level-3 {
    padding-left: 5em;
}

.table .line .counters .counter {
    width: 3em;
    text-align: center;
}

